header {
    /* border: 2px solid rgb(0, 116, 16); */
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    /* border: 2px solid red; */
    text-align: center;
    height: 100%;
    position: relative;
}

/* ==================== CTA designing ==================== */

.cta {
    /* border: 2px solid green; */
    display: flex;
    margin-top: 2.5rem;
    justify-content: center;
    gap: 1.2rem;
}

/* ==================== Header_Socials ==================== */

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after {
    content: "";
    width: 2px;
    height: 2rem;
    background: var(--color-primary);
}

/* ==================== ME IMG ==================== */

.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 20rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 2rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    /* padding: 0rem 0rem 0rem 0rem; */
}

/* ==================== SCROLL DOWN ==================== */

.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* ==================MEDIA QUERIES (MEDIUM DEVICES)==================== */

@media screen and (max-width: 1024px) {
    .container {
        height: 68vh;
    }
}


/* ==================MEDIA QUERIES (SMALL DEVICES)==================== */

@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }

    .header__socials,
    .scroll__down {
        display: none;
    }
}